body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.marketplace{
    margin:25px 0;
}
.hero{
    padding:100px 0;
    background-color:rgb(150,0,0,.5);
    background-image:url('https://images.squarespace-cdn.com/content/v1/573397cbd210b8303bfaa9c7/1463104142957-N0DQX29M7CPXXB9ELRA4/ke17ZwdGBToddI8pDm48kBazdroW2AAnSUzwP2wegpx7gQa3H78H3Y0txjaiv_0fMpLXJDHshpzSe4ApxGrjXfk2Qojd2wpVPpfTYSBVGAbrfhFEOnb3zfIgwthTN8YWZGjoBKy3azqku80C789l0nQwvinDXPV4EYh2MRzm-RTDIM6eJ7oEPoi3bzQpsO7oaHg-Gos9voqEpQLC0hR21Q/AdobeStock_3049265.jpeg?format=1500w');
    background-size:cover;

}

.hero-callout{
    color:white;
    font-size:18px;
    font-weight:bold;
}
.search-wrapper input{
    width:50%;
}

.search-wrapper button{
    display:block;
}

.search-icon{
    color:white;
    padding:5px;
    text-align:center;
    cursor:pointer;
    margin-left:10px;
}
.cta{
    background-color:darkred;
    color:white;
    transition:all 300ms;
}

.hover-class{
    background-color:white;
    color:darkred;
    transition:all 300ms;
}

.marketplace{
    border:1px solid black;
    border-radius:5px;
    padding:10px;
    background-color:#F9F9F9;
}

.marketplace .item{
    margin:10px 0;
}
.item{
    border:1px solid darkred;
    background-color:white;
}

.item img{
    width:100%;
}
.signup-form{
    border:1px solid black;
    padding:50px;
}
.consumer{
    min-height:100vh;
}


header{
    padding:5px 0;
    /* border-bottom:1px solid darkred; */
}

.headline{
    color:darkred;
}
footer{
    padding:20px 0;
    background-color:lightgray;
}
